<template>
  <v-card>
    <h1>hello!</h1>
    <v-row>
      <v-col
      cols="24"
      md="6" sm="6">

      <v-text-field v-model="ages" rules="rules" prop="rules['name']"></v-text-field>
      </v-col>

      <v-col
          cols="24"
      md="6" sm="6">
        <v-btn>验证</v-btn>
      </v-col>

    </v-row>

  </v-card>
</template>
<script>
import {required, minLength, between} from 'vuelidate/lib/validators'

export default {
  name: 'Contacts',
  data: () => ({

    snack: true,
    //界面数据
    supervisor: '',

    stdGet: '',
    stdUpdate: '',
    stdDelete: '',
    stdRegister: '',

    name: '',
    age: '100',
    ages:'130',

    rules:{
      name: [{type: 'string', required: true, message: "need value",trigger: 'blur'}, {max: 2, message: "too long"}]
    },

    selectPage: '',
    choosedTitle: '学生信息管理',

    //账号<20 8<=密码<20
    dialog: false,
    dialogDelete: false,
    headers: [
      {
        text: '学号',
        align: 'start',
        sortable: false,
        value: 'studentID',
      },
      {text: '姓名', value: 'name'},
      {text: '班号', value: 'classID'},
      {text: '分数', value: 'score'},
      {text: '等级', value: 'myrank'},
      {text: '修改', value: 'actions', sortable: false},
    ],
    stdData: [],
    editedIndex: -1,
    editedItem: {
      studentID: '',
      name: '',
      classID: '',
      score: '',
      passwd: '',
      myrank: '',
    },
    defaultItem: {
      studentID: '',
      passwd: '',
      name: '',
      score: '',
      classID: '',
      myrank: '',
    },

    test_url: "",//测试的url
    test_var1: "",//测试的var1
    test_var2: "",//测试的var2

  }),

  validations: {
    name: {
      required,
      minLength: minLength(4)
    },
    age: {
      between: between(20, 30)
    }
  },

  computed: {
    formTitle() {
      return this.editedIndex === -1 ? '新建' : '编辑'
    },
  },

  watch: {
    dialog(val) {
      val || this.close()
    },
    dialogDelete(val) {
      val || this.closeDelete()
    },
  },

  created() {
    this.initialize()
  },

  methods: {

    initialize() {
      this.stdData = [

        {
          studentID: '20383801',
          passwd: 262,
          name: 16.0,
          score: 23,
          classID: 6.0,
          myrank: '22',
        },]
    },

  },
}
</script>
